/* Forum Section Styling */
.forum-container {
    max-width: 1450px;
    margin: auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    margin-top: 40px; /* space above container */
    margin-bottom: 40px; /* space below container */

}

/* Forum Topics */
.forum-topics h3 {
    font-size: 1.8rem;
    margin-bottom: 20px;
}

.forum-topic-title {
    cursor: pointer;
    background-color: #f5f5f5;
    padding: 10px;
    margin-bottom: 10px;
    font-size: 1.4rem;
    transition: background-color 0.3s ease;
    border-radius: 8px;
}

.forum-topic-title:hover {
    background-color: #e1e1e1;
}

/* Forum Threads */
.forum-threads {
    margin-left: 20px;
    margin-bottom: 20px;
}

.post {
    background-color: #fafafa;
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.post h4 {
    margin-bottom: 5px;
}

.post p {
    margin-bottom: 10px;
}

.timestamp {
    font-size: 0.85rem;
    color: gray;
}

/* Reply Form */
.reply-form {
    margin-top: 10px;
}

.reply-form textarea {
    width: 98%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
    resize: none;
}

.reply-form .btn {
    margin-top: 10px;
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;

}

.reply-form .btn:hover {
    background-color: #45a049;
}

/* Contact Us Form Styling Inside Forum */
.contact-us-title {
    cursor: pointer;
    background-color: #f5f5f5;
    padding: 10px;
    margin-bottom: 10px;
    font-size: 1.4rem;
    transition: background-color 0.3s ease;
    border-radius: 8px;
}

.contact-us-title:hover {
    background-color: #e1e1e1;
}

.contact-us-form-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
}

.contact-logo {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 0; 
    min-height: 0;
    overflow: visible !important; /* Ensure no overflow is constraining the image */
}

.contact-form {
    flex: 2;
    padding-left: 20px;
    padding-right: 20px
}

.contact-form label {
    display: block;
    margin-bottom: 10px;
    font-weight: bold;
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
    font-size: 1rem;
}

.contact-form textarea {
    resize: none;
}

.contact-form .btn {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.contact-form .btn:hover {
    background-color: #45a049;
}

/* Error Text Styling */
.error-text {
    color: red;
    font-size: 0.9rem;
    margin-bottom: 10px;
}

/* Submitted Message Styling */
.submitted-message {
    color: green;
    font-size: 1rem;
    margin-top: 10px;
    font-weight: bold;
}
